.todo{
    width: 70%;
    min-height: 732px;
    border-radius: 50px;
    background-color: #50a87c;
    margin: auto;
    margin-top: 174px;
    display: flex;
    flex-direction: column;
    padding: 0px 44px;
    padding-bottom: 30px;
    margin-bottom: 100px;
}

.todo-header{
    margin-top: 60px;
    color: #002765;
    font-size: 36px;
    font-weight: 600px;
    line-height: 34px;
}

.todo-add{
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 45px;
}

.todo-input{
    border-radius: 20px;
    background: #edeef0;
    border: none;
    outline: none;
    width: 500px;
    height: 45px;
    padding-left: 35px;
    font-size: 20px;
}

.todo-add-btn{
    border-radius: 20px;
    background: #ff6739;
    width: 100px;
    height: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    color: #fff;
    font-size: 20px;
    font-weight: 600;
    cursor: pointer;

}